// Block elements

p {
  margin: 0.5em 0 1.2em 0;
  padding: 0;
}

// Single paragraphs shouldn't have this spacing between them.
p:only-child {
  margin: 0;
}

blockquote {
  font-size: 0.95em;
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}
